
input {
  width: 150px;
  color: black;
}
label {
  display:inline-block;
  width: 70px;
}
.border {
  border:1px solid #bbb;
}
.fat-border {
  border:3px solid #bbb;
}
.grid-container {
  margin-left: 15px;
  display: -ms-grid;
  display: grid;
  height: 100%;
  -ms-grid-columns: 1fr 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr 1fr;
  -ms-grid-rows: 1fr 1fr;
      grid-template-rows: 1fr 1fr;
  grid-template-areas: ". . . ." ". . . .";
}
.points {
  overflow-y: scroll;
}




